.card {
  width: 130px;
  height: 130px;
  margin: 1rem;
  position: relative;
  transition: all .2s ease-in-out;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  background:rgb(255, 255, 255);
  border-radius: 50%;
}

.card > .img-container > img {
  height: 100%;
}

.card:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  cursor: -webkit-grab;
  cursor: grab;
}

.card:active {
  -webkit-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}
